@keyframes linkHover {
    0% { 
        color: #942A2A;
    }
    20% { 
        color: #D28807;
    }
    40% { 
        color: #DBD409;
    }
    60% { 
        color: #0A8A12;
    }
    80% { 
        color: #0D599B;
    }
    100% { 
        color: #960D9B; 
    }
}

@keyframes pulse {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.6;
    }
    100% {
        opacity: 1;
    }
}

@keyframes line {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(160deg) translate(-1.1vw, 0.4vw);
    }
}

